import React,{useEffect  } from 'react'
import './App.css';
import Header from './Header'
import Home from'./Home.js'
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';
import Checkout from './Checkout';
import Login from './Login'
import {auth} from './firebase'
import{Value} from './StateProvider';
import Payment from './Payment'
import {loadStripe} from '@stripe/stripe-js'
import {Elements} from '@stripe/react-stripe-js'
import Orders from './Orders'

const promise=loadStripe('pk_test_51JPRrrHlfj6H9AADXxhKNjQY7qkGkve8foRdJj0xukZOFYgtmPUwgedhNLxUjyotr03AWopj0Hlkm4PZAJU6AUxB00eWQGIFxQ');

function App() {
  const [{basket,user}, dispatch]=Value();

  useEffect(()=>{
    //will only run once when the app component load...
    auth.onAuthStateChanged(authUser=>{
      console.log('the user is>>>>>' ,authUser);
      if (authUser){
        //the user just logged in/the user was logged  in...
        dispatch({
          type:'Set-User',
          user:authUser
        });
      }else{
        //the user is logged out
        dispatch({
          type:'Set-User',
          user:null
        });
      }
    })
  },[])
  return (
    <Router>
      <div className="App">
        
        <Switch>
         <Route path='/Login' > 
            <Login/>
          </Route>
          
          <Route path='/checkout' >
           <Header/> 
            <Checkout/>
           
          </Route>
          <Route path='/payment' >
           <Header/> 
           <Elements stripe={promise}>
            <Payment/>
           </Elements>
          </Route> 
          <Route path='/orders'>
            <Header/>
            <Orders/>
          </Route> 
          <Route path='/'>
            <Header/>
            <Home/>
          </Route> 
        </Switch> 
      </div
      >
    </Router> 
  );
}

export default App;
